const Precio = (props) => {

    const {price, percent_change_1h, percent_change_7d, percent_change_24h} = props.precio;
    

    return ( 
        <div className="card text-white bg-danger mb-3">
            <div className="card-header">
                Precio del Bitcoin
            </div>
            <div className="card-body">
                <h4 className="card-title">Precio Actual: S/.{price.toFixed(2)}</h4>
                <div className="d-md-flex justify-content-between">
                    <p className="card-text"><span className="font-weight-bold">Última Hora: </span>{percent_change_1h.toFixed(2)}%</p>
                    <p className="card-text"><span className="font-weight-bold">Últimas 24 Hora: </span>{percent_change_24h.toFixed(2)}%</p>
                    <p className="card-text"><span className="font-weight-bold">Últimas 7 Horas: </span>{percent_change_7d.toFixed(2)}%</p>
                </div>
            </div>
        </div>
    );
}
 
export default Precio;